import React from "react";

import Button from "@mui/material/Button";
import Accordion from "@mui/material/Accordion";

export class BucketListItemData {
  text = "";
  marked = false;
}

export class BucketList extends React.Component {
  state = {
    bucketListItems: []
  };

  constructor() {
    super();

    this.addItem = this.addItem.bind(this);
  }

  addItem() {
    console.log("Clicked");
    this.setState({
      bucketListItems: [...this.state.bucketListItems, new BucketListItemData()]
    });
  }

  render() {
    return (
      <div>
        {this.state.bucketListItems.map((e, i) => (
          <BucketListItem key={"Bucket--list--item " + i}>f</BucketListItem>
        ))}
        <Button onClick={this.addItem}>Add One</Button>
      </div>
    );
  }
}

export class BucketListItem extends React.Component {
  constructor() {
    super();
  }

  render() {
    return (
      <Accordion>
        <h1>f</h1>
      </Accordion>
    );
  }
}
